import { Input, Message, Modal } from "@arco-design/web-react";
import { useEffect, useState } from "react";
import { findById, updateById } from "../../api";
import { HouseInfo } from "../../typings";
import { useSelector } from "react-redux";
import { RootState } from "../../store";

export default function Note(props: { visible: boolean, setVisible: (visible: boolean) => void }) {
    const { visible, setVisible } = props;

    const [house, setHouse] = useState<HouseInfo>();

    const [note, setNote] = useState('');

    const id = useSelector((state: RootState) => state.house.currentHouseId);

    useEffect(() => {
        if (id) {
            findById(id).then((value) => {
                setHouse(value);
                setNote(value.note);
            });
        }
    }, [id]);
    return (
        <Modal 
            visible={visible} 
            title={`修改备注-${house?.name}`}
            onCancel={() => {setVisible(false)}} 
            onOk={async () => {
                try {
                    if (house) {
                        await updateById(house.id, { note })
                        Message.success("更新备注成功！");
                        setVisible(false);
                    }
                } catch{
                    Message.error("网络错误");
                }
            }}
        >
            <Input.TextArea value={note} onChange={setNote}/>
        </Modal>
    )
}