import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'
import { HouseInfo, HouseResult, LoadResponseData } from '../typings';
import _ from 'lodash';

export interface HouseState {
    loading: boolean;
    tableData: HouseInfo[];
    calculatorVisible: boolean;
    selectedHouse: HouseInfo[];
    currentHouseId?: number;
    total: number;
    resultVisible: boolean;
    analyzedResult: HouseResult[];
}

const initialState: HouseState = {
    loading: false,
    tableData:[],
    calculatorVisible: false,
    selectedHouse: [],
    total: 0,
    resultVisible: false,
    analyzedResult: []
}

export const HouseSlice = createSlice({
  name: 'house',
  initialState,
  reducers: {
    setCurrentHouseId(state, action: PayloadAction<number>) {
        state.currentHouseId = action.payload;
    },
    setTableData(state, action: PayloadAction<HouseInfo[]>) {
        state.tableData = action.payload;
    },
    showCalculator(state) {
        state.calculatorVisible = true;
    },
    hideCalculator(state) {
        state.calculatorVisible = false;
    },
    startLoading(state) {
        state.loading = true;
    },
    endLoading(state) {
        state.loading = false;
    },
    setTotal(state, action: PayloadAction<number>) {
        state.total = action.payload;
    },
    addSelectedHouse(state, action: PayloadAction<HouseInfo>) {
        state.selectedHouse.push(action.payload);
    },
    removeFromSelectedHouse(state, action: PayloadAction<HouseInfo>) {
        state.selectedHouse = _.remove(state.selectedHouse, (house) => !(house.id === action.payload.id));
    },
    showResult(state, action: PayloadAction<LoadResponseData[]>) {
        const result = [];
        for(let i = 0; i < action.payload.length; i++) {
            result.push({
                ...state.selectedHouse[i],
                ...action.payload[i],
            })
        }
        state.analyzedResult = result;
        state.resultVisible = true;
    },
    hideResult(state) {
        state.resultVisible = false;
    },
    updateHouseById(state, action: PayloadAction<{id: number, house: Partial<HouseInfo>}>) {
        const index = _.findIndex(state.tableData, (value) => value.id === action.payload.id);
        const newHouse = { ...state.tableData[index], ...action.payload.house };
        state.tableData.splice(index, 1, newHouse);
    },
    removeHouseById(state, action: PayloadAction<number>) {
        const index = _.findIndex(state.tableData, (value) => value.id === action.payload);
        state.tableData.splice(index, 1);
    }
  },
})

// Action creators are generated for each case reducer function
export const { updateHouseById, removeHouseById, showResult, hideResult, setCurrentHouseId, setTableData, showCalculator, hideCalculator, startLoading, endLoading, setTotal, addSelectedHouse, removeFromSelectedHouse } = HouseSlice.actions

export default HouseSlice.reducer