import { Button, Modal, Rate, Space } from "@arco-design/web-react";
import { ColumnProps } from "@arco-design/web-react/es/Table";
import { useCallback, useEffect, useMemo, useState } from "react";
import { deleteById, query, updateById } from "../../api";
import { HouseInfo } from "../../typings";
import _ from 'lodash';
import { useDispatch } from "react-redux";
import { addSelectedHouse, endLoading, removeFromSelectedHouse, removeHouseById, setCurrentHouseId, setTableData, setTotal, startLoading, updateHouseById } from "../../store/houseSlice";

function openHref (url: string) {
    window.open(url);
}

export function useColumns() {
    const dispatch = useDispatch();

    const [noteModelVisible, setNoteModelVisible] = useState(false);


    const [pageNum, setPageNum] = useState(1);

    const [pageSize, setPageSize] = useState(10);

    const [name, setName] = useState('');


    const refresh = useCallback(() => {
        dispatch(startLoading())
        query({ name, pageNum, pageSize }).then((dataFromServer) => {
            dispatch(setTableData(dataFromServer.list));
            dispatch(setTotal(dataFromServer.total));
        }).finally(() => {dispatch(endLoading())})
    }, [name, pageNum, pageSize, dispatch]);


    useEffect(() => {
        refresh();
    }, [refresh]);

    const sorter = useCallback((a: string, b: string) => {
        return a > b;
    }, []);
    const columns = useMemo<ColumnProps<HouseInfo>[]>(() => {
        return [
            {
                title: "名称",
                dataIndex: 'name',
                sorter,
                width: 400,
                fixed: 'left',
                render: (col, item) => {
                    return <Button type="text" onClick={() => openHref(item.url)}>{col}</Button>
                }
            },
            {
                title: "小区",
                width: 200,
                dataIndex: 'community',
                sorter,
            },
            {
                title: "开发商",
                width: 250,
                dataIndex: 'developer',
                sorter,
            },
            {
                title: "物业",
                width: 250,
                dataIndex: 'property',
                sorter,
            },
            {
                title: "单价(元/平米)",
                width: 100,
                sorter,
                dataIndex: 'unitPrice'
            },
            {
                title: "面积",
                width: 100,
                sorter,
                dataIndex: 'square'
            },
            {
                title: "户型",
                width: 100,
                sorter,
                dataIndex: 'type'
            },
            {
                title: "楼龄",
                width: 200,
                sorter,
                dataIndex: 'date'
            },
            {
                title: "总价（万元）",
                width: 100,
                sorter,
                dataIndex: 'totalPrice'
            },
            {
                width: 100,
                title: "朝向",
                dataIndex: 'toward'
            },
            {
                width: 200,
                title: "楼层",
                dataIndex: 'floor'
            },
            {
                title: "评分",
                sorter,
                dataIndex: 'rate',
                fixed: 'right',
                width: 200,
                render: (value, item) => {
                    return <Rate allowHalf value={parseFloat(value)} onChange={(newValue) => {updateById(item.id, { rate: newValue.toString() }).then(() => { dispatch(updateHouseById( {id: item.id, house: { rate: newValue.toString() }})) })}} />
                }
            },
            {
                title: "操作",
                fixed: 'right',
                width: 300,
                dataIndex: 'operations',
                render: (value, item) => {
                    return <Space size={10}>
                        <Button 
                            status='danger' 
                            onClick={() => { 
                                Modal.confirm({
                                    title: `删除记录`,
                                    content: `是否删除${item.name}`,
                                    onOk: () => {
                                        deleteById(item.id)
                                            .then(() => { dispatch(removeHouseById(item.id)) })
                                    }
                                })
                            }}>
                            删除
                        </Button>
                        <Button type="primary" onClick={() => { dispatch(setCurrentHouseId(item.id)); setNoteModelVisible(true) }}>备注</Button>
                        <Button type="primary" onClick={() => { dispatch(addSelectedHouse(item)) }}>加入对比</Button>
                    </Space>
                }
            }
        ]
    }, [dispatch, sorter]);

    const selectHouseColumns = useMemo(() => {
        const result =  _.cloneDeep(columns);
        result.pop();
        result.pop();
        result.push({
            title: "操作",
            dataIndex: 'operations',
            render: (value, item) => {
                return   <Button status='danger' type="primary" onClick={() => { dispatch(removeFromSelectedHouse(item)) }}>取消对比</Button>
            }
                    
        });
        return result;
    }, [columns, dispatch]);
    return { columns, selectHouseColumns, noteModelVisible, setNoteModelVisible, setPageNum, setPageSize, setName, pageNum, pageSize };
}