import { Button, Form, Drawer, InputNumber, Radio } from "@arco-design/web-react";
import { useDispatch, useSelector } from "react-redux";
import { getLoan } from "../../api";
import { RootState } from "../../store";
import { hideCalculator, showResult } from "../../store/houseSlice";
const FormItem = Form.Item;

export default function PersonInfo() {
    const [form] = Form.useForm();

    const visible = useSelector((state: RootState) => state.house.calculatorVisible);

    const selectedHouse = useSelector((state: RootState) => state.house.selectedHouse);

    const dispatch = useDispatch();

    return <Drawer 
            style={{width: 800}} 
            visible={visible} 
            footer={null} 
            onCancel={() => {dispatch(hideCalculator())}}
            title="数据对比分析"
            >
        <Form form={form} onSubmit={async (values) => {
            const houseList = selectedHouse.map(_ => ({ ..._, totalPrice: parseFloat(`${_.totalPrice}0000`) }))
            if(values.totalPrice) {
                houseList.push({ 
                    totalPrice: values.totalPrice, 
                    name:'自定义', 
                    unitPrice: '0', 
                    square: '0', 
                    type: '0', 
                    date: '', 
                    toward: '0', 
                    floor: '0',  
                    rate: '0',
                    id: 0,
                    url: '',
                    note: '',
                    community: '',
                    property: '',
                    developer: ''
                })
            }
            getLoan({
                ...values,
                houseList,
            }).then((response) => {
                dispatch(showResult(response));
            })
        }}>
            <FormItem label="自定义总价(非必填，用于对比心理价位)" field="totalPrice">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="已有资金" field="currentPrincipal">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="当前月收入" field="income">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="租房月支出" field="rent">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="其他月支出" field="outcome">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="商业贷款利率" field="syInterestRate">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="公积金贷款利率" field="gjjInterestRate">
                <InputNumber suffix=""/>
            </FormItem>
            <FormItem label="首付" field="firstPay">
                <InputNumber suffix="元"/>
            </FormItem>
            <FormItem label="公积金贷款总额" field="gjjLoan">
                <InputNumber suffix="万元"/>
            </FormItem>
            <FormItem label="商业贷款年限" field="syYear" defaultValue="20">
                <InputNumber suffix="年"/>
            </FormItem>
            <FormItem label="公积金贷款年限" field="gjjYear" defaultValue="20">
                <InputNumber suffix="年"/>
            </FormItem>
            <FormItem wrapperCol={{ offset: 5 }}>
                <Button type='primary' htmlType='submit' style={{ marginRight: 24 }}>
                提交
                </Button>
                <Button
                    onClick={() => {
                        form.resetFields();
                    }}
                >
                    重置
                </Button>
            </FormItem>
        </Form>
    </Drawer>
}