import axios from 'axios';
import { HouseInfo, LoadResponseData, LoanRequestData } from '../typings';

axios.defaults.baseURL = process.env.SERVER_HOST ?? 'https://kingworker.cn/api/house'

export async function getLoan(data: LoanRequestData): Promise<LoadResponseData[]> {
    const response = await axios.post('/loan', data);
    return response.data;
}

export async function parseHouse(url: string) {
    const response = await axios.post('/parseHouse', { url });
    return response.data;
}

export async function findAll() {
    const response = await axios.get('/findAll');
    return response.data;
}

export async function updateById(id: number, house: Partial<HouseInfo>) {
    const response = await axios.post('/updateById', {
        id,
        house
    });
    return response.data;
}

export async function findById(id: number) {
    const response = await axios.get(`/findById?id=${id}`);
    return response.data;
}

export async function deleteById(id: number) {
    const response = await axios.delete(`/deleteById?id=${id}`);
    return response.data;
}

export async function query (param: { name: string, pageNum: number, pageSize: number }) {
    const { name, pageNum, pageSize } = param;
    const response = await axios.get(`/query?name=${name}&pageNum=${pageNum}&pageSize=${pageSize}`);
    return response.data;
}