import { Table, Input, Button, Tabs, Badge } from "@arco-design/web-react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { parseHouse } from "../../api";
import { RootState } from "../../store";
import { endLoading, startLoading, setTableData, showCalculator } from "../../store/houseSlice";
import Note from "./note";
import { useColumns } from "./useColumns";

const TabPane = Tabs.TabPane;

export default function HouseList() {
    
    const {columns, selectHouseColumns, noteModelVisible, setNoteModelVisible, setPageNum, setPageSize, pageNum, pageSize} = useColumns();


    const loading = useSelector((state: RootState) => state.house.loading);

    const tableData = useSelector((state: RootState) => state.house.tableData);

    const selectHouse = useSelector((state: RootState) => state.house.selectedHouse);

    const total = useSelector((state: RootState) => state.house.total);

    const dispatch = useDispatch();


    const [activeTab, setActiveTab] = useState('1');
    return <>
        <Tabs  
            activeTab={activeTab} 
            onClickTab={(setActiveTab)} 
            style={{ paddingTop: 10 }}
            renderTabHeader={(props, DefaultHeader) => {
                return <Badge count={selectHouse.length}><DefaultHeader {...props} /></Badge>
            }}
        >
            <TabPane key='1' title='房屋列表' >
                <Input.Search 
                    loading={loading} 
                    searchButton="添加"
                    placeholder='请输入贝壳网房屋链接' 
                    style={{ width: 800, marginBottom: 10 }} 
                    onSearch={async (value) => {
                        try{
                            dispatch(startLoading())
                            const house = await parseHouse(value);
                            dispatch(setTableData([...tableData, house]))
                        } finally{
                            dispatch(endLoading());
                        }
                    }}
                />
                <Table 
                    columns={columns} 
                    loading={loading}
                    data={tableData}
                    pagination={{total, pageSize, current: pageNum, sizeOptions: [10,20,50,100]}}
                    onChange={(pagination) => {
                        setPageNum(pagination.current ?? 1);
                        setPageSize(pagination.pageSize ?? 10)
                    }}
                    scroll={{
                        x: 1600,
                        y: 800,
                    }}
                />
            </TabPane>
            <TabPane key='2' title='对比列表'>
                <Button style={{  marginBottom: 10 }} type="primary" onClick={() => { dispatch(showCalculator()) }}>开始计算</Button>
                <Table columns={selectHouseColumns} data={selectHouse} />
            </TabPane>
        </Tabs>
        <Note visible={noteModelVisible} setVisible={setNoteModelVisible} />
    </> 
}